/*
 * 轻提示
 */

.toast {
	z-index: 999;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	flex-direction: row;
	-webkit-flex-direction: row;
	justify-content: center;
	-webkit-justify-content: center;
	align-items: center;
	-webkit-align-items: center;
}
.toast p {
	max-width: 80%;
	min-width: 20%;
	padding: 0.1rem 0.2rem;
	background-color: rgba(0, 0, 0, 0.5);
	color: #FFF;
	border-radius: 0.2rem;
	font-size: 0.45rem;
	text-align: center;
}


/*
 * 弹出层
 */
.popups {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-justify-content: center;
	-webkit-align-items: center;
	justify-content: center;
	align-items: center;
	z-index: 998;
}
.popups > .mengban{
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.3);
}
.popups >.wrap {
	background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), -webkit-linear-gradient(top, rgba(252, 252, 252, 0.8), rgba(252, 252, 252, 0.8));
	background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), linear-gradient(to bottom, rgba(252, 252, 252, 0.8), rgba(252, 252, 252, 0.8));
	height: auto;
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 50%;
	-webkit-animation: 1s huajin;
	        animation: 1s huajin;
	transition-timing-function: linear;
	-moz-transition-timing-function: linear; /* Firefox 4 */
	-webkit-transition-timing-function: linear; /* Safari 和 Chrome */
	-o-transition-timing-function: linear; /* Opera */
}

@-webkit-keyframes huajin{
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}

@keyframes huajin{
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
.popups> .wrap> .title {
	text-align: center;
	height: 2rem;
	line-height: 2rem;
	font-size: 0.45rem;
	border-bottom: 1px solid #999999;
}
.contents {
	min-height: 8rem;
	max-height: 9rem;
	padding:0.5rem;
