.addMapWrap> .content{
    width: 100%;
    background-color: white;
    border-top: 1px solid #EEEEEE;
    overflow: hidden;
    position: relative;
}
.addMapWrap> .content> .looseCoat{
    transition: 0.4s;
    position: relative;
    left: 0;
}
.addMapWrap> .content> .looseCoat> .modelOne{
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
}
.addMapWrap> .content> .looseCoat> .modelTwo{
    overflow: hidden;
    position: absolute;
    top: 0;
    right: 0;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.addMapWrap> .content> .looseCoat> .modelOne> .tips{
    /* font-family: 'NotoSansCJKjp-Bold'; */
    margin-top: 17px;
    margin-bottom: 10px;
    height: 24px;
    display: flex;
    align-items:center;
    justify-content: center;
    font-size: 20px;
    font-weight: bold;
    color: #1b1b1b;
}

.addMapWrap> .content> .looseCoat> .modelOne> .tipsJan{
    height: 25px;
    margin-top: 16px;
    margin-bottom: 10px;
    display:flex;
    font-family: 'NotoSansCJKjp-Bold';
    font-size: 17px;
    justify-content: center;
    text-align: center;
    color: #1b1b1b;
}
.addMapWrap> .content> .looseCoat> .modelOne> .srollWrap{
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.addMapWrap> .content> .looseCoat> .modelOne> .srollWrap> div> .listWrap{
    margin: 0;
    display:flex;
    flex-wrap: wrap;
    /* padding: 6px; */
    list-style: none;
    margin:0 !important;
    padding-inline-start:0 !important;
    padding-right:19px;
    padding-left:20px;
    align-items: flex-start;
}
.addMapWrap> .content> .looseCoat> .modelOne>.srollWrap> div> .listWrap> li{
    /* background: #D8D8D8; */
    /* margin: 6px; */
    margin-bottom:30px;
    position:relative;
    /* float: left;
    position: relative; */
}
.addMapWrap> .content> .looseCoat> .modelOne>.srollWrap> div> .listWrap> li > .limitNumber{
    width: 20px;
    height: 20px;
    border-radius: 20px;
    text-align: center;
    line-height: 20px;
    background-color: #1B1B1B;
    color: #FFFFFF;
    position: absolute;
    right: -7px;
    bottom: -7px;
}
.addMapWrap> .content> .looseCoat> .modelOne>.srollWrap> div> .listWrap> li:last-child{
    font-size: 9px;
