ul {
	list-style: none;
}
input {
	outline: none;
}
.md-center {
	margin-left: auto;
	margin-right: auto;
}


/*
 * 轻提示
 */

.toast {
	z-index: 999;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	flex-direction: row;
	-webkit-flex-direction: row;
	-ms-flex-direction: row;
	justify-content: center;
	-webkit-justify-content: center;
	align-items: center;
	-webkit-align-items: center;
}
.toast p {
	max-width: 80%;
	min-width: 20%;
	padding: 0.1rem 0.2rem;
	background-color: rgba(0, 0, 0, 0.5);
	color: #FFF;
	border-radius: 0.2rem;
	font-size: 0.45rem;
	text-align: center;
}


/*
 * 弹出层
 */
.popups {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-justify-content: center;
	-webkit-align-items: center;
	justify-content: center;
	align-items: center;
	z-index: 998;
}
.popups > .mengban{
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.3);
}
.popups >.wrap {
	background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), linear-gradient(to bottom, rgba(252, 252, 252, 0.8), rgba(252, 252, 252, 0.8));
	height: auto;
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 50%;
	animation: 1s huajin;
	transition-timing-function: linear;
	-moz-transition-timing-function: linear; /* Firefox 4 */
	-webkit-transition-timing-function: linear; /* Safari 和 Chrome */
	-o-transition-timing-function: linear; /* Opera */
}

@keyframes huajin{
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
.popups> .wrap> .title {
	text-align: center;
	height: 2rem;
	line-height: 2rem;
	font-size: 0.45rem;
	border-bottom: 1px solid #999999;
}
.contents {
	min-height: 8rem;
	max-height: 9rem;
	padding:0.5rem;
	font-size: 0.32rem;
	overflow: hidden;
}

/*
 * 弹出层alert
 */

.f_alert {
	text-align: center;
	height: 2rem;
	line-height: 2rem;
	font-size: 0.45rem;
	border-top: 1px solid #999999;
}

.f_alert:active p {
	background-color: #f6f6f6;
	color: #1B1B1B;
}


/*
 * 弹出层confirm
 */

.f_confirm {
	text-align: center;
	height: 1rem;
	line-height: 1rem;
	font-size: 0.45rem;
	border-top: 1px solid #999999;
}


.f_confirm p {
	width: 50%;
	float: left;
}

.cancel_text {
	color: #999999;
	border-right: 1px #999999 solid;
	box-sizing: border-box;
}

.next_text {
	color: blue;
}


/*
 * 弹出层c_Input
 */

.content_input {
	min-height: 2rem;
	margin: 0 0.5rem 0.2rem 0.5rem;
	font-size: 0.32rem;
}
.lobal {
	height: 1rem;
	line-height: 1rem;
	color: #666666;
}
.content_input input {
	width: 98%;
	height: 0.8rem;
	font-size: 0.32rem;
}
.content_input input::-webkit-input-placeholder{
	font-size: 0.32rem;
}
.content_input input:-moz-placeholder{
	font-size: 0.32rem;
}
.content_input input::-moz-placeholder{
	font-size: 0.32rem;
}
.content_input input:-ms-input-placeholder{
	font-size: 0.32rem;
}
/*
 * 弹出层c_Type
 */

.c_Type {
	text-align: center;
	height: 1rem;
