/*
 * 弹出层f_layer
 */
.layers{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-justify-content: center;
	-webkit-align-items: center;
	justify-content: center;
	align-items: flex-end;
	background-color: rgba(0, 0, 0, 0.3);
	z-index: 997;
}
.layer_wrap {
	background-color: #FFF;
	width: 100%;
	height: auto;
	overflow: hidden;
}
.layer_title {
	text-align: center;
	height: 1rem;
	line-height: 1rem;
	font-size: 0.45rem;
	border-bottom: 1px solid #999999;
	color: #666666;
}
.layer_list {
	width: 100%;
	min-height: 1rem;
	max-height: 3.3rem;
	overflow-y: auto;
}
.layer_list li {
	height: 1rem;
	line-height: 1rem;
	font-size: 0.45rem;
	text-align: center;
	color: #000;
	border-bottom: 1px solid #999999;
}
.cancel_layer {
	text-align: center;
	height: 1rem;
	line-height: 1rem;
	font-size: 0.45rem;
	border-top: 1px solid #999999;
	color: #666666;
}



